// @flow
import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import QuestionTab from "@/components/Question/question-tab";
import {
  Picker,
  List,
  TextareaItem,
  ImagePicker,
  Button,
  Toast
} from "antd-mobile";
import { trim } from "@/utils/utils.js";
import { QUESTION, SUBJECT, FILE } from "@/service/api.js";
import { post } from "@/utils/request.js";
import { QESTION_TYPE } from "@/utils/constans.js";

class QuestionCreate extends Component {
  constructor(props) {
    super(props);
    const userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.state = {
      subject_id: [],
      files: [],
      content: "",
      choiceData: [],
      uploadFiles: [],
      questionTypeData: QESTION_TYPE,
      questionType: [1],
      userInfo
    };
  }

  componentDidMount() {
    this.getAllSubject();
    this.autoFocusInst.focus();
  }

  onChange = (files, type, index) => {
    if (type === "add") {
      let cur = files.slice(-1);
      this.uploadFun(cur[0].file);
    }
    this.setState({
      files
    });
  };

  uploadFun(file) {
    const { uploadFiles } = this.state;
    let formdata = new FormData();
    formdata.append("file", file);
    post(FILE.SAVE_FILE, formdata, { needqs: false }).then(res => {
      uploadFiles.push(res.data);
      this.setState({
        uploadFiles
      });
      console.log(res.data);
    });
  }
  
  /**
   * @Description: /
   */
  publish(e) {
    const {
      subject_id,
      content,
      uploadFiles,
      questionType,
      userInfo
    } = this.state;
    if (!subject_id) return Toast.info("");
    if (trim(content) === "") return Toast.info("");
    post(QUESTION.ADD_QUESTION, {
      subject_id: subject_id[0],
      content,
      type: questionType[0],
      user_id: userInfo.id,
      img_name: uploadFiles.join(",")
    }).then(e => {
      if (e.status === 200) {
        this.props.history.push("/question/questionlist");
      }
    });
  }

  getAllSubject() {
    post(SUBJECT.GET_ALL_SUBJECT, { currentPage: 1, pageSize: 100 }).then(e => {
      const list = e.data.rows;
      this.setState({
        choiceData: list.map(item => ({ label: item.title, value: item.id }))
      });
    });
  }

  render() {
    const { files, content, choiceData, questionTypeData } = this.state;
    return (
      <div className="question-create">
        <div className="question-create__box">
          <Picker
            data={questionTypeData}
            title=""
            cols={1}
            value={this.state.questionType}
            onOk={v => this.setState({ questionType: v })}
          >
            <List.Item arrow="horizontal"></List.Item>
          </Picker>
          <br />
          <Picker
            data={choiceData}
            title=""
            cols={1}
            value={this.state.subject_id}
            onChange={v => this.setState({ subject_id: v })}
            // onOk={v => this.setState({ subject_id: v })}
          >
            <List.Item arrow="horizontal"></List.Item>
          </Picker>
          <br />
          <TextareaItem
            placeholder=""
            rows={5}
            count={100}
            value={content}
            ref={ref => (this.autoFocusInst = ref)}
            onChange={content => this.setState({ content })}
          />
          <ImagePicker
            className="img-picker"
            files={files}
            onChange={this.onChange}
            multiple={true}
            onImageClick={(index, fs) => console.log(index, fs)}
            selectable={files.length < 8}
            accept="image/gif,image/jpeg,image/jpg,image/png"
          />
          <div className="question-create__btns">
            <Button
              onClick={() => {
                this.props.history.push({
                  pathname: "/question/questionlist"
                });
              }}
              size="small"
              inline
              type="primary"
              icon="check-circle-o"
            >
              
            </Button>
            <Button
              onClick={this.publish.bind(this)}
              icon="check-circle-o"
              size="small"
              type="primary"
              inline
            >
              
            </Button>
          </div>
        </div>

        <QuestionTab />
      </div>
    );
  }
}

export default withRouter(QuestionCreate);



